button{
    width: 250px;
    height: 80px;
    font-family: Roboto, sans-serif;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
    font-size: 35px;
}

.boton-clic{
    background-color: #002ead;

}

.boton-clic:hover{
    background-color: #FFFFFF;
    color: black;
    border: 2px solid #002ead;
}

.boton-reiniciar{
    background-color: #5a01a7;
}

.boton-reiniciar:hover{
    background-color: #FFFFFF;
    color: black;
    border: 2px solid #5a01a7;
}