* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body{
  background-color: #161616;
}

.app{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: Lato, sans-serif;
}

.contenedor-hooks{
  width: 80%;
  text-align: center;
  color: white;
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  margin: 20px;
}

.contenedor-hooks p{
  text-align: left;
  margin: 10px;
}

.logo{
  height: 200px;
}

.contenedor-principal-contador{
  height: 550px;
 min-width: 600px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.titulo{
  color: white;
  font-size: 1rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px;
}


@media only screen and (max-width: 660px) {
  .contenedor-principal-contador{
    min-width: auto;
  }
}